import type { Metadata } from "next";

// アプリケーション全体のメタデータ（タイトルや説明）を定義します。
// これにより、SEO対策やブラウザのタブに表示されるタイトルが設定されます。
export const metadata: Metadata = {
  title: "My RAG App",
  description: "LINE Bot & Web Chat Application",
};

// RootLayoutコンポーネント
// Next.jsのApp Routerでは、必ずこの layout.tsx が必要です。
// ここで <html> や <body> タグを定義し、全てのページの「枠組み」を作ります。
export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="ja">
      <body>
        {/* children には、各ページのコンテンツ（page.tsxなど）が差し込まれます */}
        {children}
      </body>
    </html>
  );
}
